   margin: 0;\n          color: $--color-primary;\n          padding: 8px 4px;\n          font-weight: 600;\n\n          display: flex;\n          flex-direction: column;\n\n          span {\n            font-weight: normal;\n            font-size: 12px;\n            color: $--color-text-secondary;\n          }\n        }\n\n        .attachemnt-view__signer {\n          .attachemnt-view__signer-header {\n            display: flex;\n            justify-content: space-between;\n            color: $--color-primary;\n\n            h3 {\n              padding: 0;\n              margin: 0;\n              font-weight: 700;\n              // color: $--color-gray;\n            }\n          }\n\n          h4 {\n            margin: 16px 0;\n          }\n\n          .attachemnt-view__signer-emails {\n            display: flex;\n            flex-direction: column;\n            gap: 8px;\n\n            .attachemnt-view__signer-email {\n              display: flex;\n              gap: 4px;\n\n              .el-checkbox__label {\n                display: flex;\n                gap: 8px;\n              }\n            }\n          }\n\n          .attachemnt-view__signer__status {\n            display: flex;\n            align-items: center;\n            justify-content: space-between;\n            padding: 24px 8px;\n            margin: 0;\n\n            &:hover {\n              background-color: #f6f6f6;\n            }\n\n            .attachemnt-view__signer__status-role {\n              display: flex;\n              flex-direction: column;\n              margin-left: 10px;\n              font-weight: 600;\n\n              &-name {\n                color: $--color-primary;\n              }\n            }\n\n            .attachemnt-view__signer__status-icon {\n              color: $--color-text-secondary;\n              display: flex;\n              align-items: center;\n              gap: 16px;\n\n              img {\n                width: 14px;\n                vertical-align: middle;\n                margin-bottom: 2px;\n              }\n            }\n          }\n\n        }\n      }\n    }\n  }\n}\n\n.communication-ticket-item-container__minuta-steps {\n  position: absolute;\n  bottom: 7px;\n  left: 80px;\n\n  .el-step {\n    .is-success {\n      border-color: $--color-primary !important;\n\n      .is-text {\n        background-color: $--color-primary !important;;\n      }\n    }\n\n    .is-wait {\n      border-color: $--color-primary !important;\n      color: white !important;\n    }\n\n    .is-process {\n      border-color: $--color-primary !important;\n      color: white !important;\n    }\n\n    .el-step__icon {\n      width: 14px;\n      height: 14px;\n      font-size: 6px;\n      color: $--color-primary;\n    }\n  }\n\n  &--active .el-step{\n    .is-success {\n      border-color: $--color-primary !important;\n\n      .is-text {\n        background-color: $--color-primary !important;;\n      }\n    }\n\n    .el-step__icon {\n      color: $--color-primary;\n    }\n  }\n}\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !default;\n\n$--pj-color-darker-blue: #0A0022 !default;\n$--pj-color-dark-blue: #280BA0 !default;\n$--pj-color-blue: #0756e4 !default;\n$--pj-color-light-blue: #018EF0 !default;\n$--pj-color-lighter-blue: #30A9FB !default;\n$--pj-color-white-blue: #F0F9FE !default;\n$--pj-color-disabled-blue: #9ad2f9 !default;\n\n$-pg-color-overlay: #19415F !default;\n\n$--pj-color-red: #CC4148 !default;\n$--pj-color-light-red: #EC767C !default;\n$--pj-color-disable-red: #FFF2F3 !default;\n\n$--color-primary: $--pj-color-blue !default;\n$--color-primary-light-1: mix($--color-white, $--color-primary, 10%) !default;\n$--color-primary-light-2: mix($--color-white, $--color-primary, 20%) !default;\n$--co